/* Roboto Slab */
//100
@font-face {
  display: swap;
  font-family: 'Roboto Slab';
  font-style: normal;
  font-weight: 100;
  src: url('../public//fonts/roboto-slab-v34-cyrillic_latin-100.woff2')
      format('woff2'),
    url('../public//fonts/roboto-slab-v34-cyrillic_latin-100.woff') format('woff');
}

